.countries__container {
    width: 100%;
    height: 80rem;
    display: flex;
    padding: 0 1.8rem;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 4rem;
}

.country__container {
    flex-basis: 24rem;
    background-color: transparent;
    overflow: hidden;
}

.country {
    background-color: rgb(58, 57, 57);
    height: 10rem;
    width: 90%;
    margin: 0 auto;
    padding: 1.5rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.country__title {
    color: #fff;
    margin-bottom: 1.5rem;
}

.country__photo {
    display: block;
    margin-top: 2.5rem;
    align-self: center;
    width: calc(0.25rem * 64);
    height: calc(0.25rem * 42.7);
}

@media screen and (min-width: 500px) {
    .country__photo {
        width: calc(0.3rem * 64);
        height: calc(0.3rem *42.7);
    }
}